/*==========================================================================
   #PRODUCT GRID
   ========================================================================== */

.c-product-grid {
   display: flex;
   flex-wrap: wrap;

   .c-product__img {object-fit: contain;}
}

.c-product-grid--grid {
   .c-product {flex-basis: 50%;}

   img {max-height: 60px;}
}

.c-product-grid--list {
   padding-left: $unit;
   padding-right: $unit;

   .c-product {flex-basis: 100%;}

   img {max-height: 100px;}
}